.block {
  background: #00303F;
  width: 10px;
  height: 10px;
  position: absolute;
  float: left;
  border-radius: 5px;
  transition: all 2s;
  -webkit-animation: ecg 5s linear infinite;
          animation: ecg 5s linear infinite;
    /*-webkit-animation: colorchange 2s;*/
}



